/*PAGE SET UP*/
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: "Unica One", cursive;
  margin: 0;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
body {
  background-color: #f1ffc2;
}
/*ROOT ELEMENT*/
#root {
	width: 1000px;
	height: 400px;
	-webkit-transform: translate(0%, 0%);
	        transform: translate(0%, 0%);
	border-radius: 10px;
	overflow: hidden;
	background: #fff0d9;
	-webkit-box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.46), 0px 0px 2px 1px rgba(0, 0, 0, 0.46);
	        box-shadow: 0px 0px 5px 1px rgba(0, 0, 0, 0.46), 0px 0px 2px 1px rgba(0, 0, 0, 0.46);
	margin: auto;
  margin-top: 50px;
  margin-bottom:100px !important;
}
#intro {
	width: 80%;
	margin: auto;
	margin-top: 50px;
	text-align: center;
	color: #151e3f;
	padding: 30px;
	line-height: 27px;
	border-bottom: solid #151e3f 2px;
}
#intro p {
  font-family: Arial;
}
#intro h1 {
  margin-bottom: 20px;
}
/*POMODORO CLOCK*/
#pomodoro {
  color: white;
}
/*POMODORO CONTROLS*/
.bubble {
  border-radius: 300%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.9), 0px 0px 5px rgba(0, 0, 0, 0.4);
          box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.9), 0px 0px 5px rgba(0, 0, 0, 0.4);
  position: absolute;
}
.btn {
  font-size: 400%;
  -webkit-transition: -webkit-transform 0.1s;
  transition: -webkit-transform 0.1s;
  transition: transform 0.1s;
  transition: transform 0.1s, -webkit-transform 0.1s;
}
.btn:active {
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}
.btn:hover {
  z-index: 20;
}
#time-left {
	background: #151e3f;
	font-size: 400%;
	z-index: 10;
	top: 44%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
	height: 250px;
	width: 250px;
}
#start_stop {
	background: #cc5803;
	z-index: 15;
	cursor: pointer;
	top: 81%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
	height: 100px;
	width: 100px;
}
#reset {
	background: #f7b05b;
	z-index: 15;
	cursor: pointer;
	height: 70px;
	width: 70px;
	left: 40%;
	top: 61%;
}
